var statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 'px'
import {
	balance,
	bindProduct,
	tickets
} from "../../../api/user.js"
export default {
	data() {
		return {
			statusBarHeight:statusBarHeight,
			index: -1,
			balance: {},
			page1: 1, //绑定项目
			showProductLoading: true,
			isLoadProduct: true, //是否允许滚动加载绑定项目
			scrollNodata1: false,
			product: [],
			isshowShade: false,
			page2: 1, //优惠券
			showTicketsLoading: true,
			isLoadTicket: true,
			scrollNodata2: false,
			tickets: [],
			currData: {},
			share:0, // 是否是分享进来的页面
			isMobile:0 //是否已经手机号登录绑定
		}
	},
	onLoad(opts) {
		this.index = opts.index
		this.share = opts.share || 0 
	}, 
	onShow() { 
		this.$hasToken().then(res=>{
			this.isMobile = uni.getStorageSync('isMobile') || 0
			if(uni.getStorageSync('isMobile') == 1) {
				this.doHandel() 
			}
		})
	},
	onShareAppMessage(res) {
		return {
		  title: '会员权益',
		  path: '/pages/user/user-equity/user-equity?index=0&share=1' 
		}
	},
	methods: { 
		toLogin(){
			uni.navigateTo({
				url:'/pages/login/login'
			})
		},
		// 点击自定义导航的返回跳转
		backPage(){  
			if(this.share == 1) { 
				uni.switchTab({
					url:'/pages/home/home'
				})
			} else {
				uni.navigateBack({
					
				})
			}
		},
		// 监听滚动触底
		scrollLower(){
			
			if (this.index == 1) {
				// 绑定项目触底
				this.getProduct()
			}
			if (this.index == 2) {
				// 优惠券触底
				this.getTicket()
			}
		},
		// 点击navbar
		switchNav(i) {
			if (this.index == i || this.isMobile!=1) return
			this.index = i
			this.doHandel()
		},
		doHandel() {
			if (this.index == 0) {
				this.getBalance()
			}
			if (this.index == 1 && this.page1 == 1) {
				this.getProduct()
			}
			if (this.index == 2 && this.page2 == 1) {
				this.getTicket()
			}
		},
		// 切换swiper
		changeSwiper(e) {
			if (this.index == e.detail.current ) return
			this.index = e.detail.current
			this.doHandel()
		},
		// 获取余额
		getBalance() {
			uni.showLoading({
				title: '加载中...'
			})
			balance().then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					this.balance = res.data.result
				}
			})
		},
		// 获取绑定项目
		getProduct() {
			if (!this.isLoadProduct) return
			uni.showLoading({
				title: '加载中...'
			})
			this.isLoadProduct = false
			bindProduct({
				data: {
					page_no: this.page1,
					page_size: 10
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					let list = res.data.result.items
					if (list.length == 10) {
						this.isLoadProduct = true
						this.page1++
					}
					if (list.length < 10) {
						this.showProductLoading = false
					}
					if (list.length < 10 && this.page1 > 1) {
						this.scrollNodata1 = true
					}
					this.product = [...this.product, ...list]
				}
			})
		},
		// 获取优惠券
		getTicket() {
			if (!this.isLoadTicket) return
			uni.showLoading({
				title: '加载中...'
			})
			this.isLoadTicket = false
			tickets({
				data: {
					page_no: this.page2,
					page_size: 10
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					let list = res.data.result.coupons
					if (list.length == 10) {
						this.isLoadTicket = true
						this.page2++
					}
					if (list.length < 10) {
						this.showTicketsLoading = false
					}
					if (list.length < 10 && this.page2 > 1) {
						this.scrollNodata2 = true
					}
					this.tickets = [...this.tickets, ...list]
				}
			})
		},
		// 显示弹出层
		showShade(tag, idx) {
			this.isshowShade = true
			if (tag == 'money') {
				this.currData = this.balance.present.item[idx]
			}
			if (tag == 'product') {
				this.currData = this.product[idx]
			}
			if (tag == 'ticket') {
				this.currData = this.tickets[idx]
			}
		}
	}
}
